@import url(https://fonts.googleapis.com/css2?family=Quantico:wght@700&display=swap);
*{padding:0;margin:0}html,body{min-height:100% !important;height:100%;font-family:"Quantico",sans-serif}div,input{box-sizing:border-box}:active,:hover,:focus{outline:0;outline-offset:0}.App{position:relative;display:flex;justify-content:center;align-items:center;margin:0 auto;width:100%;height:100%}.todo{margin-top:100px;display:flex;background:#fff;width:65%;height:700px;box-shadow:0px 0px 76px 7px rgba(34,60,80,.2);border-radius:20px}.todo__leftSide{background:#f0f0f0;border-radius:20px 0px 0px 20px;width:35%;height:700px;padding:50px 0px 0px 35px}.todo__leftSide-logo{font-size:50px;text-transform:uppercase;opacity:.8}.todo__leftSide-logo span{font-size:30px}.todo__leftSide-tasks{margin-top:40px}.todo__leftSide-tasks .task{position:relative;cursor:pointer;transition:all 1s;display:flex;border-radius:30px;align-items:center;height:70px;width:95%;padding:0px 0px 0px 40px;box-shadow:0px 0px 39px 5px rgba(0,0,0,.36);margin-bottom:30px;color:#fff}.todo__leftSide-tasks .task:hover{transition:all .5s;margin-top:-10px;margin-bottom:40px}.todo__leftSide-tasks .task:hover .task-remove{transition:all .2s;opacity:1}.todo__leftSide-tasks .task-remove{display:flex;justify-content:center;align-items:center;transition:all .2s;opacity:0;position:absolute;top:-10px;right:-10px;width:40px;height:40px;background-color:#fff;border-radius:100%}.todo__leftSide-tasks .task-remove img{height:20px}.todo__leftSide-addBtn{cursor:pointer;transition:all 1s;display:flex;border-radius:30px;align-items:center;background:#ba571e;background:linear-gradient(260deg, #ba571e 29%, #d77015 75%);height:70px;width:40%;padding:0px 0px 0px 40px;box-shadow:0px 0px 39px 5px rgba(0,0,0,.36);color:#fff}.todo__leftSide-addBtn:hover{transition:all .5s;margin-top:-10px}.todo__rightSide{border-radius:0px 20px 20px 0px;background:#fff;width:65%;height:100%;padding:50px 35px 55px 55px}.todo__rightSide .todo__leftSide-logo{opacity:.8}.todo__rightSide-content{font-size:22px;opacity:.8}.todo-content{display:flex;align-items:center;width:100%;height:auto;margin-top:50px}.todo-content .todo__rightSide-content{margin-right:20px}.addTask-panel{transform:translateX(-100%);display:flex;align-items:center;flex-direction:column;padding-top:10px;position:absolute;top:20%;left:0;width:250px;height:400px;background:#f0f0f0;box-shadow:0px 0px 76px 7px rgba(34,60,80,.2)}.addTask-panel-taskName{height:35px;margin-bottom:35px}.addTask-panel-taskName input{text-transform:uppercase;padding:0px 20px 0px 20px;transition:all .5s;border:0px;box-shadow:0px 0px 76px 7px rgba(34,60,80,.2);width:230px;height:35px;border-radius:15px}.addTask-panel-taskName input:hover{transition:all .5s;margin-top:-10px}.addTask-panel-gradient{width:150px;display:flex;flex-wrap:wrap;margin-bottom:20px}.addTask-panel-gradient .gradient{transition:all .5s;cursor:pointer;width:40px;height:40px;border-radius:100%;margin:0px 5px 14px 5px;box-shadow:0px 0px 20px 1px rgba(0,0,0,.466)}.addTask-panel-gradient .gradient:hover{transition:all .5s;margin-top:-10px}.addTask-panel .todo__leftSide-addBtn{width:150px;height:50px}.create__content{display:flex;justify-content:center;margin-top:100px;width:100%}.create__content-form{display:flex;align-items:center;width:100%}.create__content-form span{font-size:18px}.create__content-form input{cursor:pointer;text-transform:uppercase;padding:0px 20px 0px 20px;transition:all .5s;border:0px;background:#e6e6e6;box-shadow:0px 0px 76px 7px rgba(34,60,80,.2);width:100%;height:50px;border-radius:15px;margin:25px 0px 40px 0px}.create__content .todo__leftSide-addBtn{height:50px;width:200px}.todo-head{display:flex;align-items:center}.todo-head .todo__leftSide-logo{margin-right:20px}.edit{cursor:pointer;display:flex;justify-content:center;align-items:center;color:#fff;border-radius:100%}.edit img{width:30px;height:30px}.edit:hover{opacity:.6}.no-task{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100%;width:100%;opacity:.8}.no-task img{height:90px;width:90px;margin-bottom:20px}.orange{background:#ba571e;background:linear-gradient(260deg, #ba571e 29%, #d77015 75%)}.blue{background:#421eba;background:linear-gradient(260deg, #421eba 29%, #21989f 75%)}.yellow{background:#d3ff00;background:linear-gradient(260deg, #d3ff00 29%, #d73515 63%)}.purple{background:#0063ff;background:linear-gradient(260deg, #0063ff 22%, #b815d7 62%)}.green{background:#00ff0c;background:linear-gradient(260deg, #00ff0c 22%, #15d770 62%)}.red{background:#ff9a00;background:linear-gradient(260deg, #ff9a00 28%, #d71515 60%)}.pink{background:#ff9a00;background:linear-gradient(260deg, #ff9a00 28%, #d715d3 60%)}.blue_green{background:#00ff40;background:linear-gradient(260deg, #00ff40 28%, #4515d7 60%)}.blue_pink{background:#eb00ff;background:linear-gradient(260deg, #eb00ff 28%, #15c6d7 60%)}.open-panel{transform:translateX(0px)}
